code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.not-selected {
	user-select: none;
}


.App{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100% ;
}


header {
  color: #DD1188;
}

footer{
  background-color: rgba(33,33,33,.75) !important;
}


.modal-backdrop{
  /* From https://css.glass */
background: rgba(33, 33, 33, 0.75);
/* border-radius: 16px; */
box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
backdrop-filter: blur(8px);
-webkit-backdrop-filter: blur(8.3px);
/* border: 1px solid rgba(255, 255, 255, 0.72); */
}

.image-card__Image,
.image-card__header,
.image-card__heart,
.image-card__full-screen{
  transition: all 500ms;
}

.image-card:hover .image-card__Image {
transform: scale(1.1);
transition: all 300ms;
}

.image-card__header{
transform:  scaleY(0);
opacity: 0;
}


.image-card:hover .image-card__header{
  transform:  scaleY(1);
  opacity: 1;
}

.image-card__heart,
.image-card__full-screen{
  transform: scale(0);
  opacity: 0;

}

.image-card:hover .image-card__heart,
.image-card:hover .image-card__full-screen{
  transform: scale(1);
  opacity: 1;
}

.modal-backdrop {
  width: 100% ;
	opacity: 0;
  transform: translateY(-100%);
	visibility: hidden;
	transition: all 1000ms ease;
	z-index: -1;
	/* background-color: rgba(200, 200, 200, 0.85); */
	background-color: rgba(39, 47, 62, 0.5);
}

.modal-backdrop--show {
  transition: all 700ms ease;
	transform: translateY(0%);
	opacity: 1;
	visibility: visible;
	z-index: 1000;
}


::-webkit-scrollbar {
	width: 9px; /* Mostly for vertical scrollbars */
	height: 9px; /* Mostly for horizontal scrollbars */
}

::-webkit-scrollbar-track {
	/* Foreground */
  padding: 10px;
	background-color: #e1e1e1;
	box-shadow: 0 0  3px #999 inset;
}

::-webkit-scrollbar-thumb {
	/* Background */
	box-shadow: 0 0 3px 1px #444  inset;
	background-color: #666;
	/* border-radius: 3px; */
	transition: all .5s;
}

::-webkit-scrollbar-thumb:hover {
  box-shadow: 0 0 3px 1px #666  inset;
	background-color: #444;
}


